* {
  padding: 0px;
  margin: 0px;
  box-sizing: border-box;
}

body {
  width: 400px;
  height: 275px;
  background-image: url("../src/background.svg");
  background-size: contain;
  background-repeat: no-repeat;

}

.logo {
  position: absolute;
  left: 139px;
  top: 8px;
}

#power {
  position: fixed;
  top: 11px;
  left: 342px;
  cursor: pointer;

}

.container {
  width: 361px;
  height: 183px;
  position: absolute;
  top: 57px;
  left: 20px;
  border-radius: 15px;
  background: rgba(255, 255, 255, 0.8);
  box-shadow: 0px 4px 4px rgba(0, 0, 0, 0.1);
  backdrop-filter: blur(12px);
  display: flex;
  justify-content: center;

}

#soundValue {
  position: absolute;
  top: 19px;
  width: 105px;
  height: 18px;
  font-family: 'Poppins', sans-serif;
  font-style: normal;
  font-weight: 500;
  font-size: 12px;
  line-height: 18px;
  display: flex;
  align-items: center;
  justify-content: center;
  color: #121212;
}

#soundValue h3 {
  font-family: 'Poppins';
  font-style: normal;
  font-weight: 500;
  font-size: 13px;
  line-height: 20px;
}

#rangePanel {
  width: 100%;
  height: 42px;
  position: absolute;
  top: 55px;
  display: flex;
  justify-content: center;
  align-items: center;
}
.button{
  border: none;
  background-color: transparent;
  cursor: pointer;
}

#myRange {

  -webkit-appearance: none;
  width: 205px;
  height: 6px;
  position: absolute;
  left: 64px;
  top: 14px;
  background: linear-gradient(90deg, rgb(255, 235, 235) 60%, rgb(255, 235, 235) 60%);
  border-radius: 15px;
  outline: none;
  cursor: pointer;

}


#myRange::-webkit-slider-thumb {
  -webkit-appearance: none;
  width: 14px;
  height: 14px;
  background-color: #9B84E0;
  cursor: pointer;
  border-radius: 50%;
}


#lowSound {
  position: absolute;
  left: 36px;
  top: 8px;
}


#highSound {
  position: absolute;
  left: 292px;
  top: 8px;
}

.minVal {
  width: 19px;
  height: 17px;
  font-family: 'Poppins', sans-serif;
  font-style: normal;
  font-weight: 400;
  font-size: 11px;
  line-height: 16px;
  color: #474747;
  position: relative;
  left: -2px;
}

.maxVal {
  width: 19px;
  height: 17px;
  font-family: 'Poppins', sans-serif;
  font-style: normal;
  font-weight: 400;
  font-size: 11px;
  line-height: 16px;
  color: #262626;
  position: relative;
  left: -2px;

}

#infoSec {
  position: absolute;
  top: 115px;
  height: 38px;
  width: 100%;

}

#audioPic {
  width: 34px;
  height: 38px;
  background: #3d3d3d;
  position: absolute;
  left: 36px;
  display: flex;
  justify-content: center;
  align-items: center;
}

#audioPic img {
  width: 24px;
  height: 24px;
  /* background-color: rgb(29, 113, 81); */
}

#disc {
  width: 33px;
  height: 33px;
  background-image: url("../src/Ellipse.svg");
  background-repeat: no-repeat;
  position: relative;
  left: 51px;
  top: 2px;
  z-index: -1;
}
.disc-rotate{

  animation:spin 3s linear infinite;
}


@keyframes spin{
	from{transform:rotate(0deg)}
	to{transform:rotate(360deg)}	
}

#audioDetail {
  width: 187px;
  height: 30px;
  font-size: 25px;
  position: absolute;
  left: 126px;
  top: 4px;
}

.title {
  width: 222px;
  height: 30px;
  position: absolute;
  left: 108px;
  top: 4px;
  font-family: 'Poppins', sans-serif;
  font-style: normal;
  font-weight: 400;
  font-size: 10px;
  line-height: 15px;
  display: flex;
  align-items: center;
  color: #000000;


}

#footer {
  width: 100%;
  height: 13px;
  position: absolute;
  top: 247px;
  display: flex;
  justify-content:  center;
  font-family: 'Poppins';
  font-style: normal;
  font-weight: 400;
  font-size: 8.89605px;
  line-height: 13px;
  display: flex;
  align-items: center;
  color: #000000;
}

#footer img{
 position: relative;
  top: 2px;

}



#feedback {
  position: fixed;
  top: 257px;
  left: 347px;

}

#feedback a {
  text-decoration: none;
  font-family: 'Poppins', sans-serif;
  font-style: normal;
  font-weight: 300;
  font-size: 10px;
  line-height: 15px;
  color: rgba(44, 44, 44, 0.78);
}